require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
// import Choices from "choices.js";
// import 'choices.js/public/assets/styles/choices.min.css'
import {SeasonsHeader} from "./js/header";
import debounce from "lodash/debounce";
import Inputmask from "inputmask/lib/inputmask.js";
import {ResponsiveSlider} from "./js/ResponsiveSlider";

Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);


document.addEventListener("DOMContentLoaded", (event) => {

        const header = document.querySelector('.header');
        new SeasonsHeader(header);


        new Swiper(".reviews__swiper.swiper", {
            slidesPerView: 1,
            navigation: {
                nextEl: '.reviews__navigation .nav-next',
                prevEl: '.reviews__navigation .nav-prev',
            },
        });

        new Swiper(".resources__wrapper .swiper", {
            slidesPerView: 'auto',
            spaceBetween: 30,
            breakpoints: {
                // when window width is >= 320px
                601: {
                    spaceBetween: 30,
                    slidesPerView: 2,
                },
                801: {
                    spaceBetween: 36,
                    slidesPerView: 3,
                },
                1241: {
                    spaceBetween: 48,
                    slidesPerView: 4,
                },
            },
            navigation: {
                nextEl: '.resources__navigation .nav-next',
                prevEl: '.resources__navigation .nav-prev',
            },
        });


        new ResponsiveSlider('.important-webinars').initSwipers({
            spaceBetween: 30,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                801: {
                    spaceBetween: 36,
                },
                992: {
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
                1336: {
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                    spaceBetween: 48,
                }
            },
        });

        new ResponsiveSlider('.cards-slider').initSwipers({
            spaceBetween: 30,
            slidesPerView: 'auto',
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                801: {
                    spaceBetween: 36,
                },
                1241: {
                    spaceBetween: 48,
                }
            },
        }, true);

        new ResponsiveSlider('.partners').initSwipers({
            spaceBetween: 30,
            slidesPerView: 'auto',
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                801: {
                    spaceBetween: 36,
                },
                1241: {
                    spaceBetween: 48,
                }
            },
        }, true);

        new ResponsiveSlider('.experts-slider').initSwipers({
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            spaceBetween: 30,
            slidesPerView: "auto",
            breakpoints: {
                // when window width is >= 320px
                601: {
                    spaceBetween: 30,
                    slidesPerView: 2,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                801: {
                    spaceBetween: 36,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                1241: {
                    spaceBetween: 48,
                    slidesPerView: 4,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
            }
        }, true);


        function scrollToElementWithHeaderOffset(element, header) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight; // Позиция с учетом высоты хедера

            // Прокручиваем страницу на нужное расстояние
            window.scrollBy({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = parseInt(btn.getAttribute('data-tab'));
                        for (let block of blocks) {
                            if (parseInt(block.getAttribute('data-tab-block')) === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(block);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (parseInt(otherBtn.getAttribute('data-tab')) !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }

        const filterBox = document.querySelector('.catalog-sort');
        if (filterBox) {
            const filterBoxTop = filterBox.querySelector('.catalog-sort__current');
            const filterBoxInput = filterBox.querySelector('.catalog-sort__input');
            const currentBox = filterBox.querySelector('.catalog-sort__val');
            const filterBoxList = filterBox.querySelector('.catalog-sort__body');
            const filterBoxItems = filterBox.querySelectorAll('.catalog-sort__item-to-choose');
            const clickOutBoxHandler = ({target}) => {
                if (!target.classList.contains('catalog-sort__current') && !target.closest('.catalog-sort__current') &&
                    !target.classList.contains('catalog-sort__label') && !target.closest('.catalog-sort__label')) {
                    filterBoxList.removeAttribute('data-active');
                }
            };

            filterBoxItems.forEach(filterBoxItem => {
                filterBoxItem.addEventListener('click', (evt) => {
                    if (!filterBoxItem.hasAttribute('data-active')) {
                        const activeCur = filterBoxList.querySelector('[data-active]');
                        activeCur?.removeAttribute('data-active');
                        filterBoxItem.setAttribute('data-active', true);
                        currentBox.textContent = filterBoxItem.textContent;
                        filterBoxInput.value = filterBoxItem.textContent;
                    }
                });
            })


            filterBoxTop.addEventListener('click', (evt) => {
                if (filterBoxList.getAttribute('data-active')) {
                    filterBoxList.removeAttribute('data-active');
                } else {
                    filterBoxList.setAttribute('data-active', true);
                    document.addEventListener('click', clickOutBoxHandler);
                }
            });
        }


        const additionalBurgers = document.querySelectorAll('[data-burger-wrapper]');
        if (additionalBurgers.length) {
            additionalBurgers.forEach(additionalBurger => {
                const btns = additionalBurger.querySelectorAll('[data-burger-btn]');
                const burger = additionalBurger.querySelector('[data-burger]');
                const close = additionalBurger.querySelector('[data-burger-close]');
                const body = document.querySelector('body');

                const clickOutBurger = ({target}) => {
                    if (!target.hasAttribute('data-burger') && !target.closest('[data-burger]')) {
                        burger.removeAttribute('data-opened');
                        body.classList.remove('overlayed');
                        document.removeEventListener('click', clickOutBurger);
                    }
                }

                if (btns.length && burger) {
                    btns.forEach(btn => {
                        btn.addEventListener('click', (evt) => {
                            evt.stopImmediatePropagation();
                            evt.preventDefault();
                            burger.setAttribute('data-opened', true);
                            body.classList.add('overlayed');
                            document.addEventListener('click', clickOutBurger);
                        });
                    })
                }
                if (close && burger) {
                    close.addEventListener('click', () => {
                        burger.removeAttribute('data-opened');
                        body.classList.remove('overlayed');
                    });
                }
            });
        }


    new ResponsiveSlider('.telegram-news').initSwipers({
        spaceBetween: 30,
        slidesPerView: "auto",
        slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
        slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
        breakpoints: {
            991.98: {
                spaceBetween: 48,
            },
            1141: {
                slidesPerView: 3,
                slidesOffsetBefore: 0,
                slidesOffsetAfter: 0,
                spaceBetween: 48,
            }
        },
    });

    }
);